export default function Icons(props) {
  if(props.style == "home") {
    return <img className="icon" src="/home.svg" />;
  }
  if(props.style == "about") {
    return <img className="icon" src="/about.svg" />;
  }
  else if(props.style == "mate") {
    return <img className="icon" src="/mate.svg" />;
  }
  else if(props.style == "api") {
    return <img className="icon" src="/api.svg" />;
  }
  else {
    return <div>No icon style found!</div>;
  }
}
